<template>
  <div class="demo-print">
    <div class="demo-print__btn" @click="onPrint()">打印</div>
    <div ref="refPrintContent" class="demo-print__content">
      <div class="demo-print__left">
        <div class="demo-print__logo">
          <img
            src="data:image/png;base64,"
            alt="logo"
          />
        </div>
        <div class="demo-print__qr">
          <img src="data:image/png;base64," alt="qr" />
        </div>
      </div>
      <div class="demo-print__right">
        <dl>
          <dt>aaaa</dt>
          <dd>bbbbbbb</dd>
        </dl>
        <dl>
          <dt>aaaa</dt>
          <dd>bbbbbbb</dd>
        </dl>
        <dl>
          <dt>aaaa</dt>
          <dd>bbbbbbbbbbbbbb</dd>
        </dl>
      </div>
    </div>
  </div>
</template>

<script>
import printjs from 'print-js'
import 'print-js/dist/print.css'
import html2canvas from 'html2canvas'

export default {
  name: 'DemoPage',
  methods: {
    onPrint() {
      const printStyle = document.createElement('style')
      const printStyleContent = `
        body {
          margin: 0;
          padding: 0;
          border: 0;
        }
        img {
          display: block;
          margin: 0;
          padding: 0;
          border: 0;
        }
        @page {
          size: auto;
          margin: 0;
        }
      `
      printStyle.textContent = printStyleContent
      // document.head.appendChild(printStyle)

      html2canvas(this.$refs.refPrintContent, {
        backgroundColor: 'white',
        useCORS: true,
        foreignObjectRendering: false,
        width: 300,
        height: 180,
        scale: 5
      }).then((canvas) => {
        const url = canvas.toDataURL()
        printjs({
          printable: url,
          type: 'image',
          documentTitle: '--',
          style: printStyleContent,
          base64: 'true'
        })

        // document.head.removeChild(printStyle)
      })
    }
  }
}
</script>

<style scoped>
.demo-print__btn {
  width: 200px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  background: #fc8b8b;
  font-size: 14px;
  margin-bottom: 10px;
  cursor: pointer;
}

.demo-print__content {
  display: flex;
  width: 300px;
  height: 180px;
  overflow: hidden;
}

.demo-print__left {
  width: 105px;
  overflow: hidden;
}

.demo-print__left {
  display: flex;
  flex-direction: column;
}

.demo-print__right {
  flex: 1;
  padding: 0 5px;
}

.demo-print__logo {
  padding: 5px;
}

.demo-print__logo img {
  display: block;
  width: 100%;
}

.demo-print__qr {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
}

.demo-print__qr img {
  display: block;
  width: 100%;
}

.demo-print__right dl {
  margin: 0;
  padding: 0;
  margin-top: 5px;
  width: 90%;
}

.demo-print__right dl:first-child {
  margin: 0;
}

.demo-print__right dl dt {
  font-size: 16px;
  line-height: 20px;
  font-weight: bold;
}

.demo-print__right dl dd {
  padding: 0;
  margin: 0;
  font-size: 24px;
  line-height: 24px;
  word-break: break-all;
}
</style>
